<template>
    <div class="product-component flex align-center" @click="onSelect">
        <div class="flex-auto">
            <CheckBoxComponent
                :id="`checked-checkbox-${product.id}`"
                type="checkbox"
                v-model="isChecked"
                @click.stop="() => {}"
            />
        </div>
        <div class="flex-auto">
            <img
                width="50"
                height="50"
                class="product-image"
                :src="product.imageUrl"
            >
        </div>
        <div class="product-name flex-1">
            {{ product.name }}
        </div>
        <div class="product-price flex-auto">
            {{ displayPrice }}
            <strong v-if="product.listQuantity > 1">
                &times;{{ product.listQuantity }}
            </strong>
        </div>
    </div>
</template>

<script lang="ts">
import { computed, PropType, ref, toRefs, watch } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

import CheckBoxComponent from '@/component/item/CheckBoxComponent.vue';

import { AppStateMapper } from '@/store/AppStore';

import { AppState } from '@/store/type/AppState.type';
import { StateKeys } from '@/store/type/StateKeys';
import { Product } from '@/model/Product.type';

interface ProductComponentProps {
    product: Product,
    forSearch: Boolean,
}

export default {
    name: 'ProductComponent',

    components: {
        CheckBoxComponent,
    },

    props: {
        product: {
            type: Object as PropType<Product>,
            required: true,
        },
        forSearch: Boolean,
    },

    setup(props: ProductComponentProps) {
        const router = useRouter();
        const store = useStore<AppState>();

        const isChecked = ref<boolean>(props.forSearch
            ? AppStateMapper.mapProductId(props.product.id) in store.state.shoppingList.products
            : props.product.isChecked);

        const displayPrice = computed<string>(
            () => `${props.product.price.toFixed(2)}`);

        watch(isChecked, () => {
            if (props.forSearch) {
                if (isChecked.value) {
                    store.dispatch(
                        StateKeys.SHOPPING_LIST_PRODUCTS_ADD,
                        props.product);
                }
                else {
                    store.dispatch(
                        StateKeys.SHOPPING_LIST_PRODUCTS_REMOVE,
                        props.product.id);
                }
            }
            else {
                props.product.isChecked = isChecked.value;

                store.dispatch(
                    StateKeys.SHOPPING_LIST_PRODUCT_UPDATE,
                    props.product);
            }
        });

        return {
            isChecked,
            displayPrice,

            onSelect() {
                store.dispatch(StateKeys.CURRENT_PRODUCT_SET, props.product);
                router.push({ path: '/product', });
            },
        }
    },
}
</script>

<style lang="scss">
.product-component {
    position: relative;
    padding: 0.5rem;
    background-color: theme(white);
    cursor: pointer;

    &:first-child {
        border-top-right-radius: layout(border-radius);
        border-top-left-radius: layout(border-radius);
    }

    &:last-child {
        border-bottom-right-radius: layout(border-radius);
        border-bottom-left-radius: layout(border-radius);
    }

    &:hover,
    &.is-dragged,
    &.sortable-chosen {
        box-shadow: 1px 2px 8px rgba(0, 0, 0, 0.1);
        z-index: 1;
    }

    &.is-dragged,
    &.sortable-chosen {
        border-radius: layout(border-radius);
        border: 2px solid theme(secondary);
    }

    .product-image {
        margin-left: 0.5rem;
        border-radius: layout(border-radius);
    }

    .product-name {
        padding-left: 0.5rem;
        overflow-x: hidden;
        white-space: nowrap;
    }

    .product-price {
        padding-left: 0.25rem;
        box-shadow: -2px 0 4px 0.5rem theme(white);
    }
}
</style>