
import { computed, ref, shallowReadonly, watch } from 'vue';
import { useRoute, useRouter } from 'vue-router';

import ListIcon from '@/component/icon/BarsIcon.vue';
import SearchIcon from '@/component/icon/SearchIcon.vue';
import ShopIcon from '@/component/icon/ShopIcon.vue';

interface DashboardPage {
    title: string,
    link: string,
    icon: any,
}

export default {
    name: 'DashboardComponent',

    setup() {
        const route = useRoute();

        const dashboardPages = shallowReadonly<DashboardPage[]>([
            {
                title: 'List',
                link: '/',
                icon: ListIcon,
            },
            {
                title: 'Search',
                link: '/search',
                icon: SearchIcon,
            },
            {
                title: 'Shop',
                link: '/shop',
                icon: ShopIcon,
            },
        ]);

        return {
            dashboardPages,
        }
    },
}
