
import { computed, SetupContext } from 'vue';

export default {
    name: 'ButtonComponent',

    props: {
        isSecondary: Boolean,
    },

    setup(props: any, context: SetupContext) {
        const elementClass = computed(() => ({
            'is-secondary': props.isSecondary,
        }));

        return {
            elementClass,
        }
    },
}
