
import { computed, PropType, ref, toRefs, watch } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

import CheckBoxComponent from '@/component/item/CheckBoxComponent.vue';

import { AppStateMapper } from '@/store/AppStore';

import { AppState } from '@/store/type/AppState.type';
import { StateKeys } from '@/store/type/StateKeys';
import { Product } from '@/model/Product.type';

interface ProductComponentProps {
    product: Product,
    forSearch: Boolean,
}

export default {
    name: 'ProductComponent',

    components: {
        CheckBoxComponent,
    },

    props: {
        product: {
            type: Object as PropType<Product>,
            required: true,
        },
        forSearch: Boolean,
    },

    setup(props: ProductComponentProps) {
        const router = useRouter();
        const store = useStore<AppState>();

        const isChecked = ref<boolean>(props.forSearch
            ? AppStateMapper.mapProductId(props.product.id) in store.state.shoppingList.products
            : props.product.isChecked);

        const displayPrice = computed<string>(
            () => `${props.product.price.toFixed(2)}`);

        watch(isChecked, () => {
            if (props.forSearch) {
                if (isChecked.value) {
                    store.dispatch(
                        StateKeys.SHOPPING_LIST_PRODUCTS_ADD,
                        props.product);
                }
                else {
                    store.dispatch(
                        StateKeys.SHOPPING_LIST_PRODUCTS_REMOVE,
                        props.product.id);
                }
            }
            else {
                props.product.isChecked = isChecked.value;

                store.dispatch(
                    StateKeys.SHOPPING_LIST_PRODUCT_UPDATE,
                    props.product);
            }
        });

        return {
            isChecked,
            displayPrice,

            onSelect() {
                store.dispatch(StateKeys.CURRENT_PRODUCT_SET, props.product);
                router.push({ path: '/product', });
            },
        }
    },
}
